@import "~@/../scss/style.scss";



















































































































































































































































































































$grid-breakpoints: (
    xs: 0,
    sm: 768px,
    md: 1080px,
    lg: 1360px,
    xl: 1460px,
);

$container-max-widths: (
    sm: 720px,
    md: 1020px,
    lg: 1300px,
    xl: 1400px,
);
@import 'bootstrap';

.home {
    background: transparent linear-gradient(180deg, #112e56 0%, #537895 100%) 0% 0% no-repeat padding-box;
    margin-top: 1rem;
    margin-bottom: 1rem;
    min-height: 12rem;
    color: #f8f8f8;
    border-radius: 0.8rem;

    .home-main {
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0.8;

        &:hover {
            cursor: pointer;
            opacity: 1;
        }

        .home-icon {
            font-size: 3.2rem;
        }

        .home-value {
            margin-top: 2.5rem;
            text-align: center;

            .icon {
                font-size: 3.2rem;
                font-weight: bold;
                margin-bottom: 0.1rem;
            }

            .title {
                font-size: 1.5rem;
                padding: 0.5rem;
            }
        }
    }

    .angle-down {
        font-size: 1.2rem;
        display: flex;
        justify-content: flex-end;
        right: 0.5rem;
        opacity: 0.8;

        &:hover {
            cursor: pointer;
            opacity: 1;
        }
    }

    .child {
        font-size: 1.2rem;
        padding: 1rem 1rem;

        .title {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 1rem 1rem;
            text-align: center;
            word-wrap: break-word;
            opacity: 0.8;

            &:hover {
                cursor: pointer;
                opacity: 1;
            }
        }
    }
}
